import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Home from './components/Home'; // A pgina inicial
import Institutional from './components/Institutional'; // Pgina Institucional (exemplo)
import BankApp from './components/BankApp'; // Pgina do Banco (exemplo)
import NotFound from './components/NotFound'; // Pgina para rotas no encontradas (exemplo)
import Services from './components/Servicos';
import styled from 'styled-components';



function App() {
  return (
    <Router>
      <Routes>
        {/* Pgina Principal (Home) */}
        <Route path="/" element={<Home />} />
        {/* Outras Rotas */}
        <Route path="/servicos" element={<Services />} />
        {/* Pgina Institucional */}
        <Route path="/institucional" element={<Institutional />} />

        {/* Pgina do Banco (BankApp) */}
        <Route path="/app" element={<BankApp />} />

        {/* Pgina para rotas no encontradas */}
        <Route path="*" element={<NotFound />} />
      </Routes>
    </Router>
  );
}

export default App;

